import React from 'react';
import Typography from '@material-ui/core/Typography';
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Metadata from '../components/Metadata';
import { LicensePageMetadata } from '../configs/PageMetadata';

const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  sectionTitle: {
    paddingBottom: 10,
    fontSize: '1.20rem'
  }
}));

export default function LicensePage() {
  const classes = useStyles();
  return (
    <Layout>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          LICENSE
      </Typography>
        <Typography paragraph>
          Copyright &copy; 2011-2021 by Bipul Raman
        <br />
        All rights reserved.
        </Typography>
        <Typography paragraph>
          NOTICE:  All information contained herein is, and remains
          the property of BIPUL RAMAN.  The intellectual and technical
          concepts contained herein are proprietary to BIPUL RAMAN
          and are protected by trade secret or copyright law.
          Dissemination of this information or reproduction of this material
          is strictly forbidden unless prior written permission is obtained
          from BIPUL RAMAN.
        </Typography>
      </Paper>
      <Metadata
        Title={LicensePageMetadata.Title}
        Description={LicensePageMetadata.Description}
      />
    </Layout>
  );
}